import React, { useState, useMemo } from 'react';
import './Sidebar.css';
import { MenuFoldOutlined, MenuUnfoldOutlined } from '@ant-design/icons';

const Sidebar = ({ 
  phones, 
  selectedBrands = [], 
  onBrandSelect, 
  searchTerm = '', 
  onSearchChange,
  sortBy = 'default',
  onSortChange,
  subscriptionPlans = [],
  selectedSubscription = null,
  onSubscriptionChange = () => {}
}) => {
  // Calculate brand counts
  const brandCounts = useMemo(() => {
    const counts = {};
    phones.forEach(phone => {
      counts[phone.brand] = (counts[phone.brand] || 0) + 1;
    });
    return counts;
  }, [phones]);

  // Get unique brands and sort them
  const brands = useMemo(() => {
    const brandSet = new Set(phones.map(phone => phone.brand));
    return Array.from(brandSet).sort();
  }, [phones]);

  // Toggle sidebar collapsed state
  const [isCollapsed, setIsCollapsed] = useState(false);

  // Handle brand selection
  const toggleBrand = (brand) => {
    const newSelectedBrands = [...selectedBrands];
    const brandIndex = newSelectedBrands.indexOf(brand);
    
    if (brandIndex === -1) {
      // Add brand if not selected
      newSelectedBrands.push(brand);
    } else {
      // Remove brand if already selected
      newSelectedBrands.splice(brandIndex, 1);
    }
    
    onBrandSelect(newSelectedBrands);
  };

  // Split brands into chunks for columns
  const chunkSize = 5;
  const brandChunks = [];
  for (let i = 0; i < brands.length; i += chunkSize) {
    brandChunks.push(brands.slice(i, i + chunkSize));
  }

  return (
    <div className={`sidebar ${isCollapsed ? 'collapsed' : ''}`}>
      <div className="sidebar-toggle-container">
        <button 
          className="sidebar-toggle"
          onClick={(e) => {
            e.stopPropagation();
            setIsCollapsed(!isCollapsed);
          }}
          aria-label={isCollapsed ? 'Expand sidebar' : 'Collapse sidebar'}
        >
          {isCollapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
        </button>
      </div>
      <div className="sidebar-content">
        <div className="filter-section">
        <div className="search-container">
          <input
            type="text"
            placeholder="Search phones..."
            value={searchTerm}
            onChange={(e) => onSearchChange(e.target.value)}
            className="search-input"
          />
        </div>
        
        <div className="subscription-plan-selector">
          <h3 className="filter-title">Subscription Plan</h3>
          <div className="subscription-options">
            {subscriptionPlans.map(plan => (
              <button
                key={plan.id}
                className={`subscription-option ${selectedSubscription?.id === plan.id ? 'active' : ''}`}
                onClick={() => onSubscriptionChange(plan)}
              >
                <span className="plan-name">{plan.name}</span>
                <span className="plan-price">€{plan.price}/m</span>
              </button>
            ))}
          </div>
        </div>
        <div className="sort-controls">
          <h3 className="filter-title">Sortiraj po cijeni</h3>
          <div className="sort-options">
            <button 
              className={`sort-option ${sortBy === 'default' ? 'active' : ''}`}
              onClick={() => onSortChange('default')}
            >
              Zadano
            </button>
            <button 
              className={`sort-option ${sortBy === 'price-asc' ? 'active' : ''}`}
              onClick={() => onSortChange('price-asc')}
            >
              Najjeftiniji prvo
            </button>
            <button 
              className={`sort-option ${sortBy === 'price-desc' ? 'active' : ''}`}
              onClick={() => onSortChange('price-desc')}
            >
              Najskuplji prvo
            </button>
          </div>
        </div>
        
        <h3 className="filter-title">PROIZVOĐAČ</h3>
        <div className="brand-list">
          {brandChunks.map((chunk, chunkIndex) => (
            <div key={chunkIndex} className="brand-column">
              {chunk.map(brand => (
                <div 
                  key={brand} 
                  className={`brand-item ${selectedBrands.includes(brand) ? 'selected' : ''}`}
                  onClick={() => toggleBrand(brand)}
                >
                  <div className="brand-checkbox">
                    {selectedBrands.includes(brand) && (
                      <svg width="12" height="9" viewBox="0 0 12 9" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M1 4.5L4.33333 8L11 1" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                      </svg>
                    )}
                  </div>
                  <span className="brand-name">{brand}</span>
                  <span className="brand-count">({brandCounts[brand]})</span>
                </div>
              ))}
            </div>
          ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;