import React, { useState, useEffect, useMemo, useRef } from 'react';
import { DownOutlined } from '@ant-design/icons';
import './App.css';
import PhoneCard from './components/PhoneCard';
import PhoneSelector from './components/PhoneSelector';
import Sidebar from './components/Sidebar';
import rawPhoneData from './data/telemach_data_final.json';

// Process the phone data to match the expected format
const phoneData = rawPhoneData.map(phone => ({
  ...phone,
  // Map the new data structure to the expected format
  prices: Object.entries(phone.prices).reduce((acc, [plan, planData]) => ({
    ...acc,
    [plan]: {
      monthly_fee_24m: planData.installmentValue,
      upfront_fee: planData.price - (planData.installmentValue * 24)
    }
  }), {})
}));

// Subscription plans with updated pricing
const subscriptionPlans = [
  { id: 1, name: 'start', displayName: 'Start', price: 13.03 },
  { id: 2, name: 'top', displayName: 'Top', price: 21.41 },
  { id: 3, name: 'unlimited', displayName: 'Unlimited', price: 27.39 },
];

function App() {
  // Initialize with Unlimited subscription selected by default
  const [selectedSubscription, setSelectedSubscription] = useState(
    subscriptionPlans[2] // Default to Unlimited plan
  );
  
  const [cards, setCards] = useState([{ 
    id: 1, 
    phone: null, 
    subscription: null // Changed from defaulting to Unlimited to null
  }]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [runningTotal, setRunningTotal] = useState(0);
  const [addedPhones, setAddedPhones] = useState([]);
  const [showDropdown, setShowDropdown] = useState(false);
  const dropdownRef = useRef(null);

  // Total is now only based on the running total from added phones
  // Subscription changes won't affect the total
  useEffect(() => {
    setTotalPrice(runningTotal);
  }, [runningTotal]);

  // Handle adding a phone's monthly cost to the running total
  const handleAddToTotal = (phone) => {
    // Use the current selected subscription to calculate the phone's monthly cost
    const subscriptionType = selectedSubscription?.name || 'start';
    const subscriptionPrice = selectedSubscription?.price || 0;
    const phoneMonthly = phone.prices[subscriptionType]?.monthly_fee_24m || 0;
    
    // Total cost is phone monthly + subscription price
    const totalMonthlyCost = phoneMonthly + subscriptionPrice;
    
    const newPhone = {
      id: Date.now(),
      name: phone.title,
      amount: totalMonthlyCost, // Store the combined phone + subscription cost
      phoneData: phone,
      subscriptionType: subscriptionType,
      subscriptionPrice: subscriptionPrice
    };
    
    setAddedPhones(prev => [...prev, newPhone]);
    setRunningTotal(prev => prev + totalMonthlyCost);
  };

  // Handle removing a phone from the total
  const handleRemoveFromTotal = (id, amount) => {
    setAddedPhones(prev => prev.filter(phone => phone.id !== id));
    setRunningTotal(prev => prev - amount);
  };

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setShowDropdown(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const removeCard = (id) => {
    if (cards.length <= 1) return; // Keep at least one card
    setCards(cards.filter(card => card.id !== id));
  };

  const updateCard = (id, field, value) => {
    setCards(cards.map(card => 
      card.id === id ? { ...card, [field]: value } : card
    ));
  };

  const [selectedBrands, setSelectedBrands] = useState([]);
  const [selectedPhone, setSelectedPhone] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('default'); // 'default', 'price-asc', 'price-desc'
  
  // Filter and sort phones based on selected brands, search term, and sort order
  const filteredPhoneData = useMemo(() => {
    let filtered = [...phoneData];
    
    // Apply brand filter (case-insensitive comparison)
    if (selectedBrands.length > 0) {
      const selectedBrandsLower = selectedBrands.map(brand => brand.toLowerCase());
      filtered = filtered.filter(phone => 
        selectedBrandsLower.includes(phone.brand.toLowerCase())
      );
    }
    
    // Apply search term filter
    if (searchTerm) {
      const searchLower = searchTerm.toLowerCase();
      filtered = filtered.filter(phone => 
        phone.title.toLowerCase().includes(searchLower) ||
        phone.brand.toLowerCase().includes(searchLower)
      );
    }
    
    // Apply sorting
    if (sortBy === 'price-asc') {
      filtered.sort((a, b) => {
        const priceA = a.prices[selectedSubscription?.name || 'Unlimited']?.monthly_fee_24m || 0;
        const priceB = b.prices[selectedSubscription?.name || 'Unlimited']?.monthly_fee_24m || 0;
        return priceA - priceB;
      });
    } else if (sortBy === 'price-desc') {
      filtered.sort((a, b) => {
        const priceA = a.prices[selectedSubscription?.name || 'Unlimited']?.monthly_fee_24m || 0;
        const priceB = b.prices[selectedSubscription?.name || 'Unlimited']?.monthly_fee_24m || 0;
        return priceB - priceA;
      });
    }
    
    return filtered;
  }, [phoneData, selectedBrands, searchTerm, sortBy, selectedSubscription]);

  return (
    <div className="app">
      <header className="app-header">
        <h1>Telemach Price Calculator</h1>
        <div className="total-price">
          <span className="total-price-label">Total Monthly:</span>
          <div className="total-price-value-container">
            <span className="total-price-value">
              {totalPrice.toFixed(2)}<span className="total-price-currency">€/mo</span>
            </span>
            {addedPhones.length > 0 && (
              <div className="total-price-dropdown" ref={dropdownRef}>
                <button 
                  className="dropdown-toggle" 
                  onClick={() => setShowDropdown(!showDropdown)}
                  aria-expanded={showDropdown}
                  aria-label="Manage added phones"
                >
                  <DownOutlined />
                </button>
                {showDropdown && (
                  <div className="dropdown-menu">
                    <div className="dropdown-header">Added Phones ({addedPhones.length})</div>
                    <div className="dropdown-items">
                      {addedPhones.map(phone => (
                        <div key={phone.id} className="dropdown-item">
                          <span className="phone-name">{phone.name}</span>
                          <div className="phone-details">
                            <span className="phone-amount">{phone.amount.toFixed(2)}€</span>
                            <button 
                              className="remove-phone"
                              onClick={(e) => {
                                e.stopPropagation();
                                handleRemoveFromTotal(phone.id, phone.amount);
                              }}
                              aria-label={`Remove ${phone.name}`}
                            >
                              ×
                            </button>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </header>
      
      <div className="main-content">
        <Sidebar 
          phones={phoneData}
          selectedBrands={selectedBrands}
          onBrandSelect={setSelectedBrands}
          searchTerm={searchTerm}
          onSearchChange={setSearchTerm}
          sortBy={sortBy}
          onSortChange={setSortBy}
          subscriptionPlans={subscriptionPlans}
          selectedSubscription={selectedSubscription}
          onSubscriptionChange={(subscription) => {
            setSelectedSubscription(subscription);
            // Update subscription for all cards
            setCards(cards.map(card => ({
              ...card,
              subscription: { ...subscription }
            })));
          }}
          selectedPhone={selectedPhone}
          onPhoneSelect={(phone) => {
            setSelectedPhone(phone);
            // Add selected phone to the first card
            if (cards.length > 0) {
              updateCard(cards[0].id, 'phone', phone);
            }
          }}
        />
        
        <div className="content-area">
          <div className="phone-cards">
            {filteredPhoneData.map(phone => (
              <PhoneCard
                key={phone.product_id}
                card={{
                  id: phone.product_id,
                  phone: phone,
                  subscription: selectedSubscription
                }}
                onRemove={() => {}}
                canRemove={false}
                selectedSubscription={selectedSubscription}
                onAddToTotal={() => handleAddToTotal(phone)}
              />
            ))}
            
            {/* Keep the comparison cards at the bottom */}
            {cards.map(card => (
              <PhoneCard
                key={card.id}
                card={card}
                onUpdate={updateCard}
                onRemove={removeCard}
                canRemove={cards.length > 1}
                selectedSubscription={selectedSubscription}
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;