import React, { useState } from 'react';
import { PlusOutlined } from '@ant-design/icons';
import './PhoneCard.css';

const PhoneCard = ({ 
  card, 
  onRemove, 
  canRemove, 
  selectedSubscription, 
  onAddToTotal 
}) => {
  // Subscription plan is now managed by the parent component
  const subscription = card.subscription || selectedSubscription;
  const phone = card.phone;

  const calculateMonthlyTotal = () => {
    const phoneMonthly = getPhonePrice().monthly;
    const subscriptionMonthly = subscription?.price || 0;
    return phoneMonthly + subscriptionMonthly;
  };

  const getPhonePrice = () => {
    if (!phone) return { monthly: 0, total: 0 };
    const subscriptionType = subscription?.name || 'Unlimited';
    const monthly = phone.prices?.[subscriptionType]?.monthly_fee_24m || 0;
    return {
      monthly: monthly,
      total: monthly * 24
    };
  };

  const getPhoneImageUrl = () => {
    if (!phone) return 'https://via.placeholder.com/150x300?text=No+Image';
    
    // Use the productImage from the phone data if available
    if (phone.productImage) return phone.productImage;
    
    // Fallback to a placeholder with the phone name if no image is available
    return `https://via.placeholder.com/150x300?text=${encodeURIComponent(phone.title)}`;
  };
 
 
  const [isHovered, setIsHovered] = useState(false);
  
  const handleAddToTotal = (e) => {
    e.stopPropagation();
    if (phone && onAddToTotal) {
      onAddToTotal(calculateMonthlyTotal());
    }
  };

  if(phone) return (
    <div 
      className="phone-card"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {isHovered && (
        <button className="add-to-total-btn" onClick={handleAddToTotal} aria-label="Add to total">
          <PlusOutlined />
        </button>
      )}
      {phone.stickerText && <div className="sticker">{phone.stickerText}</div>}
      <div className="phone-image-container">
        <img 
          src={getPhoneImageUrl()} 
          alt={phone ? phone.title : 'Phone'} 
          className="phone-image"
          onError={(e) => {
            // Fallback to placeholder if image fails to load
            e.target.src = `https://via.placeholder.com/150x300?text=${encodeURIComponent(phone?.title || 'Phone')}`;
          }}
        />
      </div>
      
      <div className="phone-details">
        {phone.brand && (
          <div className="phone-brand">{phone.brand}</div>
        )}
        <h3 className="phone-title">{phone ? phone.title : `Phone #${card.id}`}</h3>
        
        <div className="divider"></div>
        
       
        

        
      </div>
      

      
      <div className="price-summary">
        <div className="price-row">
          <span>Monthly Fee:</span>
          <span className="price">€{getPhonePrice().monthly.toFixed(2)}/m</span>
        </div>
        <div className="price-row">
          <span>Phone (24 months):</span>
          <span>€{getPhonePrice().total.toFixed(2)}</span>
        </div>
        <div className="price-row">
          <span>Subscription:</span>
          <span>€{card.subscription?.price?.toFixed(2) || '0.00'}/month</span>
        </div>
        <div className="price-row total">
          <span>Monthly Total:</span>
          <span>€{calculateMonthlyTotal().toFixed(2)}/month</span>
        </div>
      </div>
    </div>
  );
};

export default PhoneCard;
